.hero {
    background:url(../img/hero-bg.jpg) top no-repeat {}
    background-size: cover;
    &-ticket {
        padding: 100px;
        background-color: $sec-color;
        color: $text-color;
        @media (max-width: 768px) {
            padding: 50px; }
        &__title {
            @include black-font;
            font-size: 40px;
            text-transform: uppercase;
            margin-bottom: 40px;
            @media (max-width: 768px) {
                font-size: 20px;
                margin-bottom: 20px; } }

        &__subtitle {
            @include black-font;
            font-size: 15px;
            display: block;
            margin-bottom: 50px;
            @media (max-width: 768px) {
                @include bold-font;
                font-size: 18px;
                margin-bottom: 35px; } }
        &__item {
            display: flex;
            align-items: flex-start;
            margin-bottom: 30px;
            @media (max-width: 768px) {
                margin-bottom: 20px; } }
        &__img {
            margin-right: 25px;
            @media (max-width: 768px) {
                margin-right: 15px; } }
        &__description {
            @include reg-font;
            font-size: 18px;
            @media (max-width: 768px) {
                font-size: 14px; } } } }


