.light-link {
    @include black-font;
    color: #fff;
    text-decoration: none;
    &:hover {
        text-decoration: none;
        color: darken(#fff, 15%); } }
.dark-link {
    @include black-font;
    color: #000;
    text-decoration: none;
    &:hover {
        text-decoration: none;
        color: lighten(#000, 15%);
        border: none; } }
.light {
    color: #fff; }
.dark {
    color: #000; }
.section {
    padding: 100px 0;
    &-dark-bg {
        background-color: $dark-bg-color; }
    &-light-bg {
        background-color: $light-bg-color; }

    &-headline {
        text-align: center;
        &__title {
            @include black-font;
            font-size: 30px;
            margin-bottom: 30px;
            @media (max-width: 768px) {
                font-size: 24px;
                margin-bottom: 20px; }
            &_big {
                font-size: 36px;
                max-width: 680px;
                margin-left: auto;
                margin-right: auto;
                @media (max-width: 768px) {
                    font-size: 28px; } } }

        &__subtitle {
            @include reg-font;
            font-size: 20px;
            display: block;
            margin-bottom: 70px;
            @media (max-width: 768px) {
                font-size: 16px;
                margin-bottom: 50px; } } } }
